body {
  --color-primary: #88c9f5;
  --color-secondary: #2980b9;
  background-color: #fff;
  margin: 0 auto;
  padding: 0;
  font-family: Arial, sans-serif;
}

.highlight {
  background-color: var(--color-primary);
  text-align: center;
  padding-top: .5rem;
}

.highlight h1 {
  color: #fff;
  text-shadow: none;
  -webkit-text-stroke: 1.5px var(--color-secondary);
  margin-bottom: 1.5rem;
  font-family: Arial, sans-serif;
}

.highlight .logo {
  vertical-align: middle;
  width: 30%;
}

.highlight .banner {
  margin-top: 2rem;
  margin-bottom: 2rem;
}

.highlight .button a {
  width: 200px;
  background-color: var(--color-secondary);
  color: #fff;
  border: 2px solid var(--color-secondary);
  border-radius: 5px;
  margin: 0 auto;
  padding: 10px;
  font-size: 25px;
  font-weight: bold;
  text-decoration: none;
  transition: all .3s;
  display: block;
}

.highlight .button a:hover {
  color: #fff;
  background-color: #333;
  border-color: #fff;
}

h1 {
  background-color: var(--color-primary);
  color: #fff;
  text-align: center;
  text-shadow: 1px 1px 2px #000;
  border: 2px solid #fff;
  margin-bottom: 20px;
  padding: 20px;
  font-size: 5vw;
  font-weight: bold;
}

nav {
  justify-content: center;
  align-items: center;
  display: flex;
}

nav ul {
  max-width: 100%;
  width: 100%;
  justify-content: center;
  margin: 0;
  padding: 0;
  display: flex;
}

nav li {
  margin-left: 10px;
  margin-right: 10px;
  list-style: none;
}

nav a {
  color: #fff;
  background-color: var(--color-secondary);
  min-width: 150px;
  box-sizing: border-box;
  text-align: center;
  border-bottom: none;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  outline: none;
  padding: 10px;
  font-size: 1.5rem;
  text-decoration: none;
  display: block;
}

nav a:hover {
  background-color: #1e6187;
}

@media only screen and (max-width: 768px) {
  nav ul {
    flex-wrap: wrap;
    justify-content: center;
  }

  nav li {
    margin-left: 10px;
    margin-right: 10px;
  }

  nav a {
    min-width: 100px;
