  margin: 0 auto;
}

.banner a {
  display: inline-block;
  position: relative;
}

.banner a:hover {
  color: #fff;
  text-shadow: -1px -1px #000, 1px -1px #000, -1px 1px #000, 1px 1px #000;
  -webkit-clip-path: polygon(50% 0%, 61% 0%, 68% 10%, 82% 10%, 87% 22%, 95% 22%, 100% 32%, 91% 39%, 100% 46%, 91% 53%, 100% 60%, 95% 70%, 87% 70%, 82% 82%, 68% 82%, 61% 92%, 50% 92%, 39% 92%, 32% 82%, 18% 82%, 13% 70%, 5% 70%, 0% 60%, 9% 53%, 0% 46%, 9% 39%, 0% 32%, 5% 22%, 13% 22%, 18% 10%, 32% 10%, 39% 0%);
  clip-path: polygon(50% 0%, 61% 0%, 68% 10%, 82% 10%, 87% 22%, 95% 22%, 100% 32%, 91% 39%, 100% 46%, 91% 53%, 100% 60%, 95% 70%, 87% 70%, 82% 82%, 68% 82%, 61% 92%, 50% 92%, 39% 92%, 32% 82%, 18% 82%, 13% 70%, 5% 70%, 0% 60%, 9% 53%, 0% 46%, 9% 39%, 0% 32%, 5% 22%, 13% 22%, 18% 10%, 32% 10%, 39% 0%);
  transform: scale(1.1);
}

.banner a:hover:after {
  content: "";
  width: 100%;
  height: 100%;
  z-index: 1;
  background-color: #00000080;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}

.banner a:hover span {
  color: #fff;
  z-index: 2;
  font-size: 3vw;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.banner a span {
  display: none;
}

